<template>
    <div class="box">
        <i class="el-icon-success"></i>
        <p class="status">{{ $t('success.status') }}</p>
        <p class="describe">{{ $t('success.describe') }}</p>
        <p class="button"><span>{{ $t('success.time') }}</span><span @click="onBack">{{ $t('success.button') }}</span></p>
    </div>
</template>
<script>
export default {
    data: () => {
        return {
            time:null 
        }
    },
    mounted() {
      this.time=  setTimeout(() => {
            this.onBack()
        }, 10000)
    },
    methods: {
        onBack() {
           clearTimeout(this.time);
            window.scrollTo({ top: 0 });
            this.$router.push('/')
        }
    }
}
</script>
<style>
